.modal {
	z-index: 1000;
	color: white;
	overflow: none;
	width: 40vw;
	height: 100vh;
	position: fixed;
	background-color: #181818;
	overflow-x: hidden;
	overflow-y: auto;
	text-align: center;
	left: 30vw;

	svg {
		filter: invert(1);
	}

	.info {
		position: relative;
		margin-top: 35vh;

		.button {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 1.5em;

			&:hover {
				cursor: pointer;
			}
		}

		.exit {
			position: absolute;
			top: 1.5em;
			right: 0;

			&:hover {
				cursor: pointer;
			}
		}

		h1 {
			padding-top: 0.5em;
			padding-left: 0.5em;
			font-weight: 1000;
			font-size: 2.5em;
			text-align: left;
			background: linear-gradient(to bottom, rgba(24, 24, 24, 0), rgba(24, 24, 24, 1) 95%)
		}

		p {
			text-align: left;
			width: auto;
			margin: 1.3em;
			font-weight: lighter;
			font-size: 0.95em;
		}
	}
}


.background {   /*Solves issue with clicks as well, blocks clicking through*/
	z-index: 100;
	height: 100vh;
	width: 100%;
	position: fixed;
	background-color: rgba(0, 0, 0, 0.5);
}

.loading-text {
	font-size: 1em;
}

.iframe {
	width: 100%;
	height: 45vh;
	position: absolute;
}


@media only screen and (max-width: 900px) {
	.modal {
		width: 100vw;
		left: 0;

		.info {
			margin-top: 40vh;

			h1 {
				padding-top: 0;
			}

			.exit {
				top: 0;
			}
		}
	}

	.iframe {
		height: 40vh;
	}

	@media only screen and (orientation: landscape) {
		.modal {

			.info {
				margin-top: 70vh;
			}
		}

		.iframe {
			height: 70vh;
		}
	}
}

@media only screen and (max-width: 450px) {
	.modal {

		.info {
			margin-top: 30vh;

			h1 {
				padding-top: 0;
			}
		}

		.exit {
			top: 0;
		}
	}

	.iframe {
		height: 30vh;
	}
}

@media only screen and (max-width: 1024px) and (min-width: 900px) and (orientation: landscape) {
	.modal {
		width: 50vw;
		left: 25vw;
		
		.info {
			margin-top: 37vh;
		}
	}
}